import type { Metadata } from "next";
import { Activity, Cpu, Brain, Shield, CheckCircle } from "lucide-react";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import Link from "next/link";

export const metadata: Metadata = {
  title: "产品展示 | GalaXense nGm无创血糖监测仪",
  description: "了解GalaXense nGm AI智糖盒的产品详情、核心技术、技术参数和认证资质",
};

const techFeatures = [
  {
    icon: Cpu,
    title: "感存算一体传感器",
    description: "采用先进的感存算一体架构，集成多波长光源、高灵敏度探测器和边缘计算单元，实现实时信号处理和智能分析。",
  },
  {
    icon: Brain,
    title: "AI深度学习算法",
    description: "基于深度神经网络的个人化血糖预测模型，持续学习用户生理特征，不断提升监测精度和个性化程度。",
  },
  {
    icon: Shield,
    title: "医用级安全标准",
    description: "通过严格的生物相容性测试，采用医疗级材料，确保长期使用安全无害。",
  },
];

const specs = [
  { label: "监测方式", value: "无创光学检测" },
  { label: "测量时间", value: "约20秒快速出结果" },
  { label: "续航时间", value: "14天超长续航" },
  { label: "防水等级", value: "IP67防水防尘" },
  { label: "连接方式", value: "4G网络连接，随时联网" },
  { label: "数据存储", value: "云端+本地双备份" },
];

const advantages = [
  "无需采血，无痛体验",
  "用户主动测量，约20秒出结果",
  "实时异常预警",
  "个性化健康报告",
  "家人远程关怀",
  "医生数据共享",
];

export default function ProductsPage() {
  return (
    <div className="min-h-screen">
      {/* Hero Section */}
      <section className="pt-20 pb-16 bg-gradient-to-br from-slate-50 to-blue-50/30">
        <div className="container-custom">
          <div className="grid lg:grid-cols-2 gap-12 items-center">
            <div>
              <span className="text-[#0066CC] font-medium text-sm tracking-wider uppercase">
                核心产品
              </span>
              <h1 className="text-4xl sm:text-5xl font-bold text-[#1A1A2E] mt-4 mb-6">
                GalaXense nGm
                <span className="text-gradient block mt-2">AI智糖盒</span>
              </h1>
              <p className="text-lg text-slate-600 leading-relaxed mb-8">
                融合感存算一体传感器与人工智能技术的下一代无创血糖监测设备，
                为糖尿病患者和健康管理人群提供舒适、精准、智能的血糖管理解决方案。
              </p>
              <div className="flex flex-wrap gap-3 mb-8">
                {["无创监测", "AI智能", "主动测量", "精准校准"].map((tag) => (
                  <span
                    key={tag}
                    className="px-4 py-2 bg-white rounded-full text-sm font-medium text-slate-600 shadow-sm"
                  >
                    {tag}
                  </span>
                ))}
              </div>
              <Button
                size="lg"
                className="bg-gradient-primary hover:opacity-90 text-white px-8 rounded-xl"
                asChild
              >
                <Link href="/contact">预约体验</Link>
              </Button>
            </div>
            <div className="relative">
              <div className="aspect-square max-w-md mx-auto bg-white rounded-3xl shadow-2xl p-8 border border-slate-100">
                <div className="aspect-square bg-gradient-to-br from-slate-100 to-slate-50 rounded-2xl flex items-center justify-center">
                  <div className="text-center">
                    <Activity className="w-32 h-32 text-[#0066CC] mx-auto mb-4" />
                    <p className="text-slate-400">产品实物图</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Core Technology */}
      <section className="py-20 lg:py-32 bg-white">
        <div className="container-custom">
          <div className="text-center max-w-3xl mx-auto mb-16">
            <span className="text-[#00A896] font-medium text-sm tracking-wider uppercase">
              核心技术
            </span>
            <h2 className="text-3xl sm:text-4xl font-bold text-[#1A1A2E] mt-3 mb-4">
              三大核心技术突破
            </h2>
            <p className="text-slate-600 text-lg">
              通过传感器硬件、AI算法、临床医学的深度融合，实现无创血糖监测的技术革新
            </p>
          </div>

          <div className="grid md:grid-cols-3 gap-8">
            {techFeatures.map((feature, index) => (
              <Card key={feature.title} className="p-8 border-slate-100">
                <div className="w-16 h-16 rounded-2xl bg-gradient-primary flex items-center justify-center mb-6">
                  <feature.icon className="w-8 h-8 text-white" />
                </div>
                <h3 className="text-xl font-bold text-[#1A1A2E] mb-3">
                  {feature.title}
                </h3>
                <p className="text-slate-600 leading-relaxed">
                  {feature.description}
                </p>
              </Card>
            ))}
          </div>
        </div>
      </section>

      {/* Specifications */}
      <section className="py-20 lg:py-32 bg-slate-50">
        <div className="container-custom">
          <div className="grid lg:grid-cols-2 gap-12 items-center">
            <div>
              <span className="text-[#FF6B35] font-medium text-sm tracking-wider uppercase">
                技术参数
              </span>
              <h2 className="text-3xl sm:text-4xl font-bold text-[#1A1A2E] mt-3 mb-8">
                产品规格
              </h2>
              <div className="space-y-4">
                {specs.map((spec) => (
                  <div
                    key={spec.label}
                    className="flex justify-between items-center py-4 border-b border-slate-200"
                  >
                    <span className="text-slate-600">{spec.label}</span>
                    <span className="font-semibold text-[#1A1A2E]">{spec.value}</span>
                  </div>
                ))}
              </div>
            </div>
            <div>
              <span className="text-[#0066CC] font-medium text-sm tracking-wider uppercase">
                产品优势
              </span>
              <h3 className="text-2xl font-bold text-[#1A1A2E] mt-3 mb-6">
                与传统血糖仪对比
              </h3>
              <div className="space-y-4">
                {advantages.map((advantage, index) => (
                  <div key={index} className="flex items-center gap-3">
                    <CheckCircle className="w-6 h-6 text-[#00A896] flex-shrink-0" />
                    <span className="text-slate-700">{advantage}</span>
                  </div>
                ))}
              </div>
              <div className="mt-8 p-6 bg-gradient-to-r from-[#0066CC]/10 to-[#00A896]/10 rounded-2xl">
                <p className="text-sm text-slate-600 mb-2">测量精度说明</p>
                <p className="text-sm text-slate-600 mb-4">
                  通过传统有创血糖仪几次校准后，测量精度接近传统血糖仪水平
                </p>
                <div className="space-y-3">
                  <div>
                    <div className="flex justify-between text-sm mb-1">
                      <span>GalaXense nGm（校准后）</span>
                      <span className="font-semibold text-[#0066CC]">接近传统血糖仪</span>
                    </div>
                    <div className="h-2 bg-slate-200 rounded-full overflow-hidden">
                      <div className="h-full w-[95%] bg-gradient-primary rounded-full" />
                    </div>
                  </div>
                  <div>
                    <div className="flex justify-between text-sm mb-1">
                      <span>传统血糖仪</span>
                      <span className="font-semibold text-slate-500">参考标准</span>
                    </div>
                    <div className="h-2 bg-slate-200 rounded-full overflow-hidden">
                      <div className="h-full w-[95%] bg-slate-400 rounded-full" />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Certifications */}
      <section className="py-20 lg:py-32 bg-white">
        <div className="container-custom text-center">
          <span className="text-[#0066CC] font-medium text-sm tracking-wider uppercase">
            权威认证
          </span>
          <h2 className="text-3xl sm:text-4xl font-bold text-[#1A1A2E] mt-3 mb-12">
            专业认证，值得信赖
          </h2>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
            {[
              "国家高新技术企业",
              "ISO13485质量管理体系",
              "CE认证",
              "多项专利技术",
            ].map((cert) => (
              <div
                key={cert}
                className="p-6 bg-slate-50 rounded-2xl border border-slate-100"
              >
                <div className="w-16 h-16 mx-auto mb-4 bg-white rounded-xl flex items-center justify-center shadow-sm">
                  <Shield className="w-8 h-8 text-[#0066CC]" />
                </div>
                <p className="font-medium text-[#1A1A2E]">{cert}</p>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* CTA */}
      <section className="py-20 bg-gradient-primary">
        <div className="container-custom text-center">
          <h2 className="text-3xl font-bold text-white mb-6">
            想了解更多产品信息？
          </h2>
          <p className="text-white/90 text-lg mb-8 max-w-2xl mx-auto">
            我们的专业团队将为您提供详细的产品介绍和技术解答
          </p>
          <Button
            size="lg"
            className="bg-white text-[#0066CC] hover:bg-white/90 px-8 rounded-xl"
            asChild
          >
            <Link href="/contact">立即咨询</Link>
          </Button>
        </div>
      </section>
    </div>
  );
}
